import axios from 'axios'
import store from '@/store'
import { MessageBox, Message } from 'element-ui'
import {removeToken , removeKey} from '@/utils/auth'
import router from '@/router'


axios.defaults.baseURL = process.env.VUE_APP_BASE_URL


// 
axios.interceptors.request.use(
  config => {
    var strArr = config.url.split("/")
    var str = strArr[strArr.length-1]
    // headertoken
// console.log('',store.state.app.workflowurl,str)


    if( str != 'ticketLogin' && str != 'getUserInfo' && str != 'login'){

    	config.headers['userId'] = sessionStorage.getItem("userId");
    	config.headers['token'] = sessionStorage.getItem("token");

    }
    return config;
  }, function (error) {
    // 
    // Message.error({message: '!'});

    return Promise.reject(error);
  });

//  RESPONSE 
axios.interceptors.response.use(data=> {
//==============    ==================
	// console.log('',data.data.successful)

  // 200error
  if (data.status && data.status == 200 && typeof(data.data.successful)!='undefined'
  		&&!data.data.successful) {
  	console.log('200error',data.data.resultHint)
  	if(data.data.resultHint != ''){
  		// Message.error({message:data.data.resultHint});
  		Message({
                showClose: true,
                dangerouslyUseHTMLString: true,
                message: data.data.resultHint,
                type: 'error'
        });
  	}

    if(data.data.resultHint == ''){//
    	removeToken()
	  	removeKey()
	  	sessionStorage.clear()
	  	let loca = store.state.user.locationUrl
      if(loca.indexOf("?") != -1){
            loca = loca.split("?")[0];
        }
        console.log('',loca)
        // let loca = encodeURIComponent('http://'+location.host+'/isclogin')
        window.location.href=store.state.user.iscUrl+"/isc_sso/login?service="+loca
        // window.location.href="http://sso.isc.com:8989/isc_sso/login?service="+loca
	  	// router.replace({
	   //      path: '/isclogin'
	   //  })
    }
    return data;
  }
  //
  // if(data.status && data.status == 200 && data.data=='authorizeFail'){
  // 	// location.replace(`/user/login`)
  // 	removeToken()
  // 	removeKey()
  // 	router.replace({
  //       path: '/isclogin'
  //   })
  //   return;

  // }
  return data;
}, err=> {
//==============    ====================
   if (err && err.response) {
        switch (err.response.status) {
            case 400: err.message = '(400)'; break;
            case 401: err.message = '(401)'; break;
            case 403: err.message = '(403)'; break;
            case 404: err.message = '(404)'; break;
            case 408: err.message = '(408)'; break;
            case 500: err.message = '(500)'; break;
            case 501: err.message = '(501)'; break;
            case 502: err.message = '(502)'; break;
            case 503: err.message = '(503)'; break;
            case 504: err.message = '(504)'; break;
            case 505: err.message = 'HTTP(505)'; break;
            default: err.message = `(${err.response.status})!`;
        }
    } else {
        err.message = '!'
    }

    Message.error({message:err.message } )

  return Promise.resolve(err);
})

