            fileRes.forEach(function(item){
              if(item.key == 1){
                if(item.fileName.length>0){
                  let fileObj = {};
                  fileObj.name = item.fileName
                  fileObj.url = ''
                  _this.reportFileId = item.fileId
                  _this.reportFileName = item.fileName
                  _this.fileList.push(fileObj)
                }
                if(item.itemFileList){
                  let fileList = item.itemFileList
                  fileList.forEach(function(childItem){
                    let fileObjChild = {};
                    fileObjChild.name = childItem.fileName
                    fileObjChild.url = ''
                    let fileParam = {}
                    fileParam.fileId = childItem.fileId
                    fileParam.fileName = childItem.fileName
                    _this.fjIds.push(fileParam)
                    _this.fileList2.push(fileObjChild)
                  })
