			}
		},
		handleExceed2(files, fileList) {
			let _this = this;
			if (files.length > 5) {
				_this.$message.warning(` 5  ${files.length}  ${files.length + fileList.length} `);
			}
		},
    //
    uploadSuccess(response,file,fileList){
      let _this = this
      this.fileUploadLoading = false
      if(response.successful){
      	_this.reportFileId = response.resultValue.fileId
        _this.reportFileName = response.resultValue.fileName
        _this.fileList = fileList
      }
          
    },

    // 
		uploadSuccess2(response){
			let _this = this;
			if(response.successful){
				let fileParam = {}
				fileParam.fileId = response.resultValue.fileId
				fileParam.fileName = response.resultValue.fileName
				_this.fjIds.push(fileParam)
			}else{
				this.$message({
					showClose: true,
					message: '',
					dangerouslyUseHTMLString: true,
					type: 'error'
				});
			}
		},

     //
    deleteReport(){
      let _this = this
      let formData = new FormData();
      if(_this.fileList.length==0){
        this.$message({
              showClose: true,
              message: "",
              type: 'warning'
        });
         return false
      }
      _this.$confirm('','',{
        confirmButtonText:'',
        cancelButtonText:'',
        type:'warning'
      }).then(() => {
        formData.append("itemId", _this.uploadReportItemId);
        deleteReport(formData).then(res =>{
            if(res.data.successful){
                  _this.fileList = []
                  this.$message({
                    showClose: true,
                    message: "",
                    type: 'success'
                  });
                  _this.getTableWithTitle(_this.currentPage,_this.currentNum)
                }else{
                  let msg = res.data.resultHint
                  this.$message({
                    showClose: true,
                    message: msg,
                    dangerouslyUseHTMLString: true,
                    type: 'error'
                  });
                }
                _this.loading =false
        
        })
      })
      
    },

    //
    saveAudtiInfo() {
        let params = {
            "user": this.$route.query.iscUserId,
            "workItemID": this.$route.query.workItemID
        }
        this.loadingBtn1 = true
        finishWorkItem(params).then(res => {
            if(res.data.status == '0'){
              this.$alert('', '', {
                confirmButtonText: '',
                callback: () => {
                  parent.GetChildValue("true");
                }
              });
            }else {
              this.loadingBtn1 = false
              this.$message.error(res.data.message);
            }
        })
    },

