    //         _this.userSpecialityArr = str.split(",");
    //       }
    //       console.log(response);
    //     }
    //   });
    // },
    //
    formartDate(param) {
      var time = new Date(param);
      var y = time.getFullYear();
      var m = time.getMonth() + 1;
      var d = time.getDate();
      // var h = time.getHours();
      // var mm = time.getMinutes();
      // var s = time.getSeconds();
      // return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
      return y + "-" + this.add0(m) + "-" + this.add0(d);
    },
    add0(m) {
      return m < 10 ? "0" + m : m;
    },
    switchFilter() {
      this.isShowFilter = !this.isShowFilter;
    },
    //
    getColumnTypeArr(labelList) {
      let map = new Map();
      for (let i = 0; i < labelList.length; i++) {
        let temp = labelList[i];
        map.set(temp.colDname, temp.colType);
      }
      return map;
    },

    //
    getTableWithTitle(size) {
      //page,size
      let _this = this;
      _this.loading = true;
      queryTitle().then(response => {
        _this.loading = false;
        if (response.data.successful) {
          //
          _this.tableLabel = response.data.resultValue
          //
          _this.dictMap = _this.getDictArr(_this.tableLabel);
          //
          _this.columnTypeMapDict = _this.getColumnTypeArr(_this.tableLabel);
          //id
          let processInstID = _this.$route.query.processInstID;
          //
          getItemInfoByCreatorAndProcessId({ processId: processInstID }).then(
            res => {
              if (res.data.successful) {
                console.log(res);
                let itemInfo = res.data.resultValue;
                // 
                // _this.expertAdvice.itemId = itemInfo.itemId;
                // if(itemInfo.isBatchPackage == 1){
                //   _this.expertAdvice.isBatchPackage =true
                // }else{
                //   _this.expertAdvice.isBatchPackage =false
                // }
                _this.uploadReportItemId = itemInfo.itemId
                _this.uploadReportData.itemId = itemInfo.itemId
               
                _this.tableDataShow = _this.getShowDateTable(itemInfo, _this.dictMap, _this.columnTypeMapDict);
                 //
                let param = {}
                param.itemPhaseDet = _this.tableDataShow[0].itemPhaseDet
                param.itemId = _this.tableDataShow[0].itemId
                getAuditResult2(param).then(response=>{
                    console.log(response)
                    if(response.data.successful){
                        let data = response.data.resultValue
                        _this.rejectInfo.info = data.auditAdvice
                        _this.rejectInfo.fileId = data.auditFileId
                    }
                })
                
              }
            }
          );
        } else {
          this.$message.error("");
        }
      });
    },
    getDictArr(labelList) {
      //id
      //
      let _this = this;
      let map = new Map();
      for (let i = 0; i < labelList.length; i++) {
        let temp = labelList[i];
        if (temp.codeList != null) {
          map.set(temp.colDname, temp.codeList);
        }
      }
      return map;
    },
    //
    getShowDateTable(tableData, dictMap, columnDictMap) {
        let _this = this
        let arr = [];
        for (let i = 0; i < tableData.length; i++) {
            let tempObj = tableData[i];
            //
            let copyObj = new Object();
            //key
            for (let [key, value] of Object.entries(tempObj)) {
                //console.log(key + ':' + value);
                let lowerKey = key.toLowerCase()
                if (dictMap.has(key)) {
                    let dictValueList = dictMap.get(key);
                    for (let j = 0; j < dictValueList.length; j++) {
                        let tempObj2 = dictValueList[j];
                        if (tempObj2.codeValue == value) {
                            copyObj[key] = tempObj2.codeName;
                        }
                    }
                } else if (dictMap.has(lowerKey)) {
                    let dictValueList = dictMap.get(key.toLowerCase);
                    for (let j = 0; j < dictValueList.length; j++) {
                        let tempObj2 = dictValueList[j];
                        if (tempObj2.codeValue == value) {
                            copyObj[key] = tempObj2.codeName;
                        }
                    }
                } else if (columnDictMap.get(key) == "date" || columnDictMap.get(key) == "month"
                    || columnDictMap.get(key) == "year") {
                    if(value!=null&&value!=''){
                        copyObj[key] = _this.formartDate(value, columnDictMap.get(key));
                    }  
                } else {
                    copyObj[key] = value;
                }

                if (key == "itemClass") {
                    copyObj["itemClassT"] = value
                }

                if (key == "conDept") {
                    copyObj["conDept"] = value
                }
                if (key == "itemLevel") {
                    copyObj["itemLevelT"] = value
                }

                //add by liangwu
                if (key == "specDetailClass") {
                    copyObj["specDetailClassT"] = value
                }

                if (key == "specClass") {
                    copyObj["specClassT"] = value
                }
                //add end
            }
            if (typeof (tempObj.children) != 'undefined' && tempObj.children != null && tempObj.children.length > 0) {
                let tempArr = _this.getShowDateTable(tempObj.children, dictMap, columnDictMap)
                copyObj.children = tempArr
            }
            arr.push(copyObj);
        }
        return arr;
    },
  }
};
