<template>
  <div class="container">
    
    <div class="main clearfix" :style="{'margin-left':$store.state.app.isCollapse ? '84px':'240px'}">
      
      <div class="main_container">
      <el-tabs v-model="activeName" type="card" class="special" @tab-click="handleClick">
        <el-tab-pane 
        v-for="item in specialList"
        :label="item.codeName" 
        :name="item.codeValue">
          <!--  -->
          <el-tabs v-model="infrastructureName" type="card" class="special" v-if="item.codeValue == 1" @tab-click="handleClick">
            <el-tab-pane 
            v-for="initem in infrastructureList"
            :label="initem.codeName" 
            :name="initem.codeValue">
              
            </el-tab-pane>
            
          </el-tabs>
