          </el-table-column>
          
        </el-table> -->
        <el-table
          class="storage-table"
          :data="tableData"
          :span-method="idSpanMethod"
          border
          :header-cell-style="{background:'#359691',color:'#fff'}"
          :height="'60vh'"
          style="width: 100%; margin-top: 20px;">
          <el-table-column
            prop="labelCode"
            label=""
            width="180">
            <template slot-scope="scope" >
              <el-input v-model="scope.row.labelCode" @change="changeinp"></el-input>
            </template>
          </el-table-column>
          <el-table-column
            prop="labelName"
            label="">
          </el-table-column>
          <el-table-column
            prop="labelCode1"
            label="">
            <template slot-scope="scope" >
              <el-input v-model="scope.row.labelCode1" @change="changeinp"></el-input>
            </template>
          </el-table-column>
          <el-table-column
            prop="labelName1"
            label="">
          </el-table-column>
          <el-table-column
            prop="labelCode2"
            label="">
            <template slot-scope="scope" >
              <el-input v-model="scope.row.labelCode2" @input="changeNumber(scope.row.labelCode2,scope.row)" ></el-input>
            </template>
          </el-table-column>
          <el-table-column
            prop="labelName2"
            label="">
          </el-table-column>
        </el-table>
        <div style="margin-top:10px;text-align:center">
          <el-button type="primary" @click="SubmissionTable"></el-button>
            <!-- <el-button ></el-button> -->
          <el-button ><router-link to="/storageOrder"> </router-link></el-button>

        </div>
      </div>
      
     
    </div>
    
