</template>

<script>
import { mapGetters } from 'vuex'
import {specList,getStandard,savePrjCmLabelDefine} from '@/api/data'


export default {
  name: 'storageManagement',
  data() {
    return {
      infrastructureName:'',
      infrastructureList:[],
      activeName:'',
      isWeight1:false,
      isWeight2:false,
      specialList:[],
      spanArr:[],
      spanArrChild:[],
      permissionData:[{
        'deviceId': '',
        'taxerName': '',
        'taxerId': '',
        'rowspan': [1, 1]
      }, {
        'deviceId': '',
        'taxerName': '',
        'taxerId': '',
        'rowspan': [6, 1]
      }, {
        'deviceId': '',
        'taxerName': '',
        'taxerId': '',
        'rowspan': [0, 3]
      }, {
        'deviceId': '',
        'taxerName': '',
        'taxerId': '',
        'rowspan': [0, 0]
      }, {
        'deviceId': '',
        'taxerName': '',
        'taxerId': '',
        'rowspan': [0, 0]
      }, {
        'deviceId': '',
        'taxerName': '',
        'taxerId': '',
        'rowspan': [0, 2]
      }, {
        'deviceId': '',
        'taxerName': '',
        'taxerId': '',
        'rowspan': [0, 0]
      }],
      tableData: [{
          id: '12987122',
          parentWeight:'1',
          childWeight:'3',

          name: '',
          amount1: '234',
          amount2: '3.2',
          amount3: 10
        }, {
          id: '12987122',
          parentWeight:'1',
          childWeight:'3',

          name: '',
          amount1: '165',
          amount2: '4.43',
          amount3: 12
        }, {
          id: '12987124',
          parentWeight:'2',
          childWeight:'1',

          name: '',
          amount1: '324',
          amount2: '1.9',
          amount3: 9
        }, {
          id: '12987125',
          parentWeight:'2',
          childWeight:'2',

          name: '',
          amount1: '621',
          amount2: '2.2',
          amount3: 17
        }, {
          id: '12987126',
          parentWeight:'2',
          childWeight:'2',
          name: '',
          amount1: '539',
          amount2: '4.1',
          amount3: 15
          }
        ],
        tabMap: new Map,
        tabMap2:new Map,
    }
  },
  computed: {
    ...mapGetters([
      'roles',
      'token'
    ])
  },
  components: {
    
  },
 
  mounted(){
    this.getSpanArr(this.tableData)
    this.getSpecList()
  },
  created(){
    
  },
  methods: {
    changeNumber(num,row){
      console.log(num)
      if(num < 0 || num > 100){
        row.labelCode2 = 0
        this.$message({
          showClose: true,
          message: '0100',
          type: 'warning'
        });
      }
    },
    accSub(arg1,arg2){
        var r1,r2,m,n;
        try{r1=arg1.toString().split(".")[1].length}catch(e){r1=0}
        try{r2=arg2.toString().split(".")[1].length}catch(e){r2=0}
        m=Math.pow(10,Math.max(r1,r2));
        //
        n=(r1>=r2)?r1:r2;
        return ((arg1*m+arg2*m)/m).toFixed(n);
    },
    SubmissionTable(){
      const _this = this

      for (var i = 0; i < _this.tableData.length; i++) {//
        if(i != 0){//
          if(_this.tableData[i].lableId === _this.tableData[i - 1].lableId){
            _this.tableData[i].labelCode = _this.tableData[i - 1].labelCode
          }
          if(_this.tableData[i].lableId1 === _this.tableData[i - 1].lableId1){
            _this.tableData[i].labelCode1 = _this.tableData[i - 1].labelCode1
          }
        }
        
      }
      _this.getTabMap(_this.tableData)
      //

        let tempAdd = 0
        _this.tabMap.forEach(function(value,key){
            value = parseFloat(value)
console.log(value);

            // tempAdd = tempAdd += value
            tempAdd = _this.accSub(tempAdd,value)
});
        if(tempAdd == 1){
          _this.isWeight1 = true
        }else{
          _this.isWeight1 =false
        }
        //
        try {
        _this.tabMap2.forEach(function(value,key){
          console.log(value)
            let tempAdd2 = 0

            value.forEach(function(val,k){
              val = parseFloat(val)
              // tempAdd2 += val
              tempAdd2 = _this.accSub(tempAdd2,val)

            })
            console.log('tempAdd2',tempAdd2)
            if(tempAdd2 == 1){
              _this.isWeight2 = true
            }else{
              _this.isWeight2 =false
              throw Error()
            }
        })
      }catch (e) {}
      console.log('isWeight1',_this.isWeight1,'isWeight2',_this.isWeight2)
      if(_this.isWeight1 && _this.isWeight2){
          savePrjCmLabelDefine(_this.tableData).then(response => {
            console.log(response)
            if(response.data.successful){
              _this.$message({
                showClose: true,
                message: '',
                type: 'success'
              });
            }
          })
          
          _this.$router.push({path: '/storageManagement'})
      }else{
        if(!_this.isWeight1){
          _this.$message({
            showClose: true,
            message: '1,',
            type: 'warning'
          });
        }else if(!_this.isWeight2){
          _this.$message({
            showClose: true,
            message: '1,',
            type: 'warning'
          });
        }
        
      }
    },
    changeinp(){
      console.log(this.tableData)
    },
    handleClick(tab, event){
      const _this = this
      console.log(tab,event)
      if(tab.name != '01' ){//1
        this.getStandData(tab.name)
      }else{
        this.getStandData(this.infrastructureName)//tab
      }
    },
    getStandData(id){
      const _this = this
      getStandard({'specId':id}).then(response => {
        if(response.data.successful){
          console.log()
          _this.getSpanArr(response.data.resultValue)//
          _this.tableData = response.data.resultValue
          
        }
      })
    },
    getSpecList(){
      specList().then(response => {
        console.log('response',response)
        if(response.data.successful){
          this.specialList = response.data.resultValue
          console.log('specialList',this.specialList)

          this.activeName = this.specialList[0].codeValue
          // this.getStandData(this.activeName)
          this.infrastructureList = this.specialList[0].codeList//
          
          this.infrastructureName = this.infrastructureList[0].codeValue
         
          this.getStandData(this.infrastructureName)//tab

        }
      })

    },
    // objectSpanMethod ({ row, column, rowIndex, columnIndex }) {
    //   if ([0, 1].indexOf(columnIndex) !== -1) {
    //     if (column.rowspan !== 0) {
    //       // 
    //       if (columnIndex === 0) {
    //         return {
    //           rowspan: row.rowspan[0],
    //           colspan: 1
    //         }
    //       }
    //       // 
    //       return {
    //         rowspan: row.rowspan[1],
    //         colspan: 1
    //       }
    //     } else {
    //       return {
    //         rowspan: 0,
    //         colspan: 0
    //       }
    //     }
    //   }
    // },
    idSpanMethod({ row, column, rowIndex, columnIndex }){
      if(columnIndex == 0 || columnIndex == 1){
         const _row = this.spanArrChild[rowIndex];
          const _col = _row > 0 ? 1 : 0;
          return {
            rowspan: _row,
            colspan: _col
          };
      }else if(columnIndex == 2 || columnIndex == 3){
        const _row = this.spanArr[rowIndex];
          const _col = _row > 0 ? 1 : 0;
          return {
            rowspan: _row,
            colspan: _col
          };
      }
    },
    getSpanArr(data) {
      this.spanArr = [];
      this.spanArrChild=[]
      for (var i = 0; i < data.length; i++) {
        if (i === 0) {
          // 0
          this.spanArr.push(1);
          this.spanArrChild.push(1);
          this.pos = 0;
          this.posChild = 0
        } else {
          
          if(data[i].lableId === data[i - 1].lableId){
            this.spanArrChild[this.posChild] += 1;
            this.spanArrChild.push(0);

          }else{
            this.spanArrChild.push(1);
            this.posChild = i;
          }

          if (data[i].lableId1 === data[i - 1].lableId1) {
            // parentWeightpush 0  parentWeight 
            this.spanArr[this.pos] += 1;
            this.spanArr.push(0);
          } else {
            // push 1
            this.spanArr.push(1);
            this.pos = i;
          }
        }
      }
      console.log('spanArrChild',this.spanArrChild)
    },
    getTabMap(data){ 

      let map = new Map()
      let map2 = new Map()
 
      const _this = this
      console.log(data)
      for(let i=0;i<data.length;i++){
        let temp = data[i];

      //  _this.tabMap.set(temp.lableId,temp.labelCode);
        if(!map.has(temp.lableId)){
            map.set(temp.lableId,temp.labelCode)
        }

        if(map2.has(temp.lableId)){
            let tempMap = map2.get(temp.lableId)
            if(!tempMap.has(temp.lableId1)){
                tempMap.set(temp.lableId1,temp.labelCode1)
            }
        }else{
           
            let tmpMap = new Map()
            tmpMap.set(temp.lableId1,temp.labelCode1)
            map2.set(temp.lableId,tmpMap)
        }
      }
      _this.tabMap = map
      _this.tabMap2 = map2

      console.log(_this.tabMap,_this.tabMap2)
    },


   
  }
}
</script>
