<template>
  <div>
    <!--  -->
    <div class="table-item-box">
      <div :class="showDrawer?'table-wrapper-run':'table-wrapper-init'">
        <el-table
          class="project"
          :data="tableDataShow"
          v-loading="loading"
          :height="tableHeight"
          border
          :header-cell-style="{background:'#359691',color:'#fff'}"
          cell-class-name="column"
          :highlight-current-row="true"
          row-key="itemId"
          ref="itemTable"
          :tree-props="{children: 'children'}"
        >
          <el-table-column type="selection" width="45"></el-table-column>
          <el-table-column type="index" width="50" label=""></el-table-column>
          <el-table-column
            v-for="(item, index) in tableLabel"
            v-if="item.colDname != 'itemName' && item.showType == 1"
            :key="index"
            :prop="item.colDname"
            :label="item.dispName"
            :show-overflow-tooltip="true"
            min-width="200"
          ></el-table-column>
          <el-table-column
            align="left"
            :show-overflow-tooltip="true"
            v-else-if="item.colDname == 'itemName'"
            :label="item.dispName"
            min-width="200"
          >
            <template slot-scope="scope">
              <span v-if="scope.row.isBatchPackage ==1" >{{ scope.row.itemName }}</span>
              <span v-else class="blue" @click="showDetailDialog(scope.row)">{{ scope.row.itemName }}</span>
            </template>
          </el-table-column>
        </el-table>
        <div class="table-item-box" style="height: 50% !important;">
          <div class="table-box" :class="showDrawer?'table-wrapper-run':'table-wrapper-init'">
            <!--  -->
            <div class="counter-sign">
                <h3 class="dialog_tit" ></h3>
                <el-radio-group v-model="countersignParam.agreeType" disabled>
                    <el-radio label="1"></el-radio>
                    <el-radio label="0"></el-radio>
                </el-radio-group>
                <el-input type="textarea" class="textarea"  v-model="countersignParam.auditOpinion" disabled></el-input>
                <h3 class="dialog_tit" ></h3>
                <div>
                    <a v-if="countersignParam.auditFileId!=''" style="color: #409EFF;" :href="GLOBALURL+'/prjStFeaAudit/exportReport?feaAuditid='+countersignParam.auditFileId+'&token='+token" ></a>
                </div>
            </div>
            <div slot="footer" class="dialog-footer option">
              <div class="div">
                <el-button type="primary" @click="saveAudtiInfo" :loading="loadingBtn3"></el-button>
                <el-button type="primary" @click="saveAudtiInfo" :loading="loadingBtn1"></el-button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div :class="showDrawer?'item-add-wrapper-run':'item-wrapper-init'">
        <!--    itemaddcom -->
        <item-add-com
          v-if="showDrawer"
          :key="timer"
          :showData="showData"
          :showAddDialog="showDrawer"
          :isShowQuery="showQuery"
          :tableHeightTab="tableHeight"
          @changeStatus="changeStatus"
        ></item-add-com>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapGetters } from "vuex";
import SelectTree from "@/components/treeSelect.vue";
import itemDialog from "@/views/project/item.vue";
import ItemAddCom from "@/views/project/itemAddCom";

import {
  auditBySpeciality,
  getTreeDeptCode,
  getUserDept,
  queryItemTitle,
  queryAll,
  getProcessStatus,
  updateProcessFinished,
  getItemInfoByProcessInstId,
  queryTitle,
  getItemInfoByProcessInstIdWithBatchGroup,
  checkPlan,
  createProcess,
  saveWorkItem,
  isAutoSelectExpert,
  expertAudit,
  getLeaderAdvise,
  expertCountersign
} from "@/api/data";

import { queryBySpeciality, getSpecialityIds } from "@/api/data";
import querys from "@/components/querys";

import { finishWorkItem} from "@/api/workflow";

export default {
  name: "questions",
  data() {
    return {
        isAuto:false,
      itemId:"",
      showQuery:false,
      tableHeightTab:"",
      timer: "",
      showAddDialog: false, //-  
      showData: {
        itemId: null, //id
        specClass: "", //
        specClassName: "", //
        oldPath: "", //
        tableLabelArr: "", //
        phash: "1", //1= 2= 3= 0=
        parentItemId: null //id-
      },
      showDrawer: false,
      deptCode: "",
      isClearable: true, // 
      isAccordion: true, // 
      valueId: "", // ID
      props: {
        // 
        value: "id",
        label: "name",
        children: "children",
        code: "deptCode"
        // disabled:true
      },
      deptArr: [],
      //
      loadingBtn1: false,
      loadingBtn2: false,
      loadingBtn3: false,
      //
      countersignParam:{
          "id":"",
          "agreeType":"",
          "auditOpinion":"",
          "auditFileId":""
      },
      isShowQuery: false,
      dialogVisibleL: false,
      dialogLoading: false,
      loading: false,
      lableList: [{ value: "" }],
      totalNum: 0,
      currentPage: 1,
      currentNum: 10,
      year: "",
      dialogFormVisible: false,
      form: {},
      options: [],
      value: "",
      formLabelWidth: "140px",
      input: "",
      // 
      tableData: [],
      // 
      tableLabel: [],
      //
      param: {
        createOrg: "", //
        itemName: "", //
        itemType: "", //
        pageSize: 10,
        speciality: "", //
        voltLevel: "", //
        ghYear: "", //
        itemSource: ""
      },
      dictMap: new Map(),
      tableDataShow: [],
      saveData: {
        kssj: ""
      },
      //
      pleanTableLabel: [],
      planLabel: {},
      //id
      tempItemId: "",
      //
      tempItemName: "",
      //
      itemShow: false,
      saveLabel: {},
      //
      reportFileName: "",
      //
      columnTypeMap: new Map(),
      isShowFilter: false,
      //

      tableHeight: "65vh",
      //
      userSpecialityArr: [],
       token:"",
      userId:"",
      itemSpeciality:"",
      tempIsBatch:false
    };
  },
  components: {
    querys,
    SelectTree,
    ItemAddCom
  },
  watch: {
    deptCode() {
      this.param.createOrg = this.deptCode;
      this.getTable(this.currentPage, this.currentNum);
    }
  },
  created() {
    //
    this.tableHeight=window.innerHeight-220;
  },


  mounted() {
    this.getSpecialityIds();
    this.getTableWithTitle(1, 10);
    this.userId = this.$route.query.iscUserId;
    this.token = sessionStorage.getItem('token');
  },
  computed: {
    ...mapGetters([
      // 'token',
    ])
  },
  methods: {
    cancel() {
        top.opener=null;
        top.close();     //,   
        // parent.window.close();
        // var index = parent.layer.getFrameIndex(window.name);
        // parent.layer.close(index);
    },
    getValue(value) {
      this.deptCode = value;
    },
    // 
    changeStatus(status) {
      this.showDrawer = status;
      console.log("status" + status);
    },
    //
    showDetailDialog(row) {
      this.showData.itemId = row.itemId;
      this.showData.phash = 0;
      this.showDrawer = true;
      this.timer = new Date().getTime();
    },
    //
    getTreeDeptCode() {
      let _this = this;
      getUserDept().then(response => {
        let valueId = response.data.resultValue;
        getTreeDeptCode().then(response => {
          console.log(response);
          if (response.data.successful) {
            _this.valueId = valueId;
            _this.deptArr = response.data.resultValue;
          }
        });
      });
    },

    //
    saveAudtiInfo() {
        let params = {
            "user": this.$route.query.iscUserId,
            "workItemID": this.$route.query.workItemID
        }
        this.loadingBtn1 = true
        this.loadingBtn3 = true
        finishWorkItem(params).then(res => {
            if(res.data.status == '0'){
              let signParam = {
                itemId:this.tempItemId,
                isBatchPackage:this.tempIsBatch
              }
                expertCountersign(signParam).then(result => {
                  this.loadingBtn3 = false
                  this.loadingBtn1 = false
                  if (result.data.successful){
                        this.$alert('', '', {
                          confirmButtonText: '',
                          callback: () => {
                            parent.GetChildValue("true");
                          }
                        });
                  }else {
                      let error = result.data.resultHint;
                      this.$message.error(error);
                  }
                });
                
                
            }else {
              this.loadingBtn1 = false
              this.loadingBtn3 = false
              this.$message.error(res.data.message);
            }

        })
    },

    getSpecialityIds() {
      let _this = this;
      getSpecialityIds().then(response => {
        if (response.data.successful) {
          let str = response.data.resultValue;
          if (str != "") {
            _this.userSpecialityArr = str.split(",");
          }
          console.log(response);
        }
      });
    },
    //
    formartDate(param) {
      var time = new Date(param);
      var y = time.getFullYear();
      var m = time.getMonth() + 1;
      var d = time.getDate();
      // var h = time.getHours();
      // var mm = time.getMinutes();
      // var s = time.getSeconds();
      // return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
      return y + "-" + this.add0(m) + "-" + this.add0(d);
    },
    add0(m) {
      return m < 10 ? "0" + m : m;
    },
    switchFilter() {
      this.isShowFilter = !this.isShowFilter;
    },
    //
    getColumnTypeArr(labelList) {
      let map = new Map();
      for (let i = 0; i < labelList.length; i++) {
        let temp = labelList[i];
        map.set(temp.colDname, temp.colType);
      }
      return map;
    },

    //
    getTableWithTitle(size) {
      //page,size
      let _this = this;
      _this.loading = true;
      queryTitle().then(response => {
        _this.loading = false;
        if (response.data.successful) {
          //
          _this.tableLabel = response.data.resultValue
          //
          _this.dictMap = _this.getDictArr(_this.tableLabel);
          //
          _this.columnTypeMapDict = _this.getColumnTypeArr(_this.tableLabel);
          //id
          let processInstID = _this.$route.query.processInstID;
          //
          getItemInfoByProcessInstIdWithBatchGroup({ processInstID: processInstID }).then(
            res => {
              if (res.data.successful) {
                console.log(res);
                let itemInfo = res.data.resultValue;
                _this.tempItemId = itemInfo.itemId
                // 
                 let param = {}
                if(itemInfo.isBatchPackage == 1){
                  param.isBatchPackage = true
                  _this.tempIsBatch = true
                }else{
                  param.isBatchPackage = false
                  _this.tempIsBatch = false
                }
                _this.countersignParam.id = itemInfo.feaAuditid
               
                param.itemId = itemInfo.itemId
                getLeaderAdvise(param).then(response=>{
                    console.log(response)
                    let data = response.data.resultValue
                    _this.countersignParam.agreeType = data.isAudit+""
                    _this.countersignParam.auditOpinion = data.auditAdvice
                    _this.countersignParam.auditFileId = data.fileId
                })
                let arr =[]
                if (itemInfo != null) {
                  arr.push(itemInfo);
                }
                _this.tableDataShow = _this.getShowDateTable(arr, _this.dictMap, _this.columnTypeMapDict);
              }
            }
          );
        } else {
          this.$message.error("");
        }
      });
    },
    getDictArr(labelList) {
      //id
      //
      let _this = this;
      let map = new Map();
      for (let i = 0; i < labelList.length; i++) {
        let temp = labelList[i];
        if (temp.codeList != null) {
          map.set(temp.colDname, temp.codeList);
        }
      }
      return map;
    },
    //
    getShowDateTable(tableData, dictMap, columnDictMap) {
        let _this = this
        let arr = [];
        for (let i = 0; i < tableData.length; i++) {
            let tempObj = tableData[i];
            //
            let copyObj = new Object();
            //key
            for (let [key, value] of Object.entries(tempObj)) {
                //console.log(key + ':' + value);
                let lowerKey = key.toLowerCase()
                if (dictMap.has(key)) {
                    let dictValueList = dictMap.get(key);
                    for (let j = 0; j < dictValueList.length; j++) {
                        let tempObj2 = dictValueList[j];
                        if (tempObj2.codeValue == value) {
                            copyObj[key] = tempObj2.codeName;
                        }
                    }
                } else if (dictMap.has(lowerKey)) {
                    let dictValueList = dictMap.get(key.toLowerCase);
                    for (let j = 0; j < dictValueList.length; j++) {
                        let tempObj2 = dictValueList[j];
                        if (tempObj2.codeValue == value) {
                            copyObj[key] = tempObj2.codeName;
                        }
                    }
                } else if (columnDictMap.get(key) == "date" || columnDictMap.get(key) == "month"
                    || columnDictMap.get(key) == "year") {
                    if(value!=null&&value!=''){
                        copyObj[key] = _this.formartDate(value, columnDictMap.get(key));
                    }  
                } else {
                    copyObj[key] = value;
                }

                if (key == "itemClass") {
                    copyObj["itemClassT"] = value
                }

                if (key == "conDept") {
                    copyObj["conDept"] = value
                }
                if (key == "itemLevel") {
                    copyObj["itemLevelT"] = value
                }

                //add by liangwu
                if (key == "specDetailClass") {
                    copyObj["specDetailClassT"] = value
                }

                if (key == "specClass") {
                    copyObj["specClassT"] = value
                }
                //add end
            }
            if (typeof (tempObj.children) != 'undefined' && tempObj.children != null && tempObj.children.length > 0) {
                let tempArr = _this.getShowDateTable(tempObj.children, dictMap, columnDictMap)
                copyObj.children = tempArr
            }
            arr.push(copyObj);
        }
        return arr;
    },
  }
};
</script>
