const whiteList = ['/login'] // no redirect whitelist

router.beforeEach(async(to, from, next) => {
  console.log(getRouter)
  

  // set page title
  // document.title = getPageTitle(to.meta.title)

  // determine whether the user has logged in
  // const hasToken = getToken()
  var hasToken = sessionStorage.getItem("token")
  console.log(hasToken)
  let keyPath = getUrlKey("path",window.location.href)
 
  if (hasToken && hasToken != 'undefined' ) {
    console.log('hasToken',hasToken)
    
    if (to.path === '/login' || to.path === '/isclogin') {
      // if is logged in, redirect to the home page
      if(keyPath && keyPath.length){//path  path
        if(keyPath == "projectApproval" ||keyPath == "projectSubmitApproval" 
        ||keyPath == "planAssessmentApproval" ||keyPath == "planAssessAgainApproval" 
        ||keyPath == "expertsConfirmApproval" ||keyPath == "expertsAgainApproval"
        ||keyPath == "reviewCommentsApproval" ||keyPath == "collectCommentsApproval"
        ||keyPath == "countersignatureApproval" ||keyPath == "perfectFileApproval" ||keyPath == "uploadFileApproval") {
          let processInstID = getUrlKey("processInstID",window.location.href)
          let workItemID = getUrlKey("workItemID",window.location.href)
          let activityInstID = getUrlKey("activityInstID",window.location.href)
          let iscUserId = getUrlKey("iscUserId",window.location.href)
          next({ path: keyPath,query: { path:keyPath,processInstID:processInstID,workItemID:workItemID,activityInstID:activityInstID,iscUserId:iscUserId}})
        } else {
          next({ path: keyPath,query: { path:keyPath}})
        }
        
      }else{
        next({ path: '/' })
      }
      
    } else {


        if (store.getters.roles.length === 0 ) { // user_info
          if(store.getters.isIscLogin){//isc
            store.dispatch('user/iscGetInfo').then(res => { // info
              // const roles = res.data.role;

              // store.dispatch('permission/generateRoutes', { roles }).then(() => { // 
              //   console.log('addRoutes',store.getters.addRoutes)
               
              //   router.addRoutes(store.getters.addRoutes) // 
                
              //   // debugger
              //   next(to); // hack addRoutes
                
              // })
              //**************************************************************
              if (!getRouter) { //
                if (!getObjArr('router')) {
                  // easy-mock,axios
                  // axios.get('https://www.easy-mock.com/mock/5a5da330d9b48c260cb42ca8/example/antrouter').then(res => {
                  console.log('beforeEach  res')
                  getRouter = res //
                  //******** */  **********/
                  // getRouter = fakeRouter.router
                  saveObjArr('router', getRouter) //localStorage

                  routerGo(to, next) //
                  // })
                } else { //localStorage
                  res = getObjArr('router') //
                  console.log(res)
                  routerGo(to, next)
                }
                next(to);
                 
              } else {
                next()
              }
//********************************************************************************
            }).catch(err => {
              console.log(err);
            });
          }else if(store.getters.isSimulateLogin){//
            console.log('permissiongetinfo')
            store.dispatch('user/simulateGetInfo').then(res => { // info
              const roles = res.data.role;

              store.dispatch('permission/generateRoutes', { roles }).then(() => { // 
                console.log('addRoutes',store.getters.addRoutes)
               
                router.addRoutes(store.getters.addRoutes) // 
                
                // debugger
                next(to); // hack addRoutes
                
              })
            }).catch(err => {
              console.log(err);
            });
          }
          else{//
            store.dispatch('user/getInfo').then(res => { // info
              // const roles = res.data.role;

              // store.dispatch('permission/generateRoutes', { roles }).then(() => { // 
              //   // console.log('addRoutes',store.getters.addRoutes)
               
              //   router.addRoutes(store.getters.addRoutes) // 
                
              //   // debugger
              //   next(to); // hack addRoutes
                
              // })
//**************************************************************
              if (!getRouter) { //
                if (!getObjArr('router')) {
                  // easy-mock,axios
                  // axios.get('https://www.easy-mock.com/mock/5a5da330d9b48c260cb42ca8/example/antrouter').then(res => {
                  console.log('beforeEach  getRouter')
                  getRouter = fakeRouter.router //
                  saveObjArr('router', getRouter) //localStorage

                  routerGo(to, next) //
                  // })
                } else { //localStorage
                  getRouter = getObjArr('router') //
                  console.log(getRouter)
                  routerGo(to, next)
                }
                // router.addRoutes(getRouter)
                next(to);
                console.log(router.options.routes)
                 
              } else {
                next()
              }
//********************************************************************************
            }).catch(err => {
              console.log(err);
            });
          }
          
        } else {
          console.log(to,router)
          console.log('')

          next() // 404
        }
    }
  } else {
    /* has no token*/
    console.log("notoken")
    if (whiteList.indexOf(to.path) !== -1) {
      // in the free login whitelist, go directly
      next()
    } else {
      console.log("sdfsdfsdfdfsdf",store.getters.isIscLogin)
      // other pages that do not have permission to access are redirected to the login page.
       if(store.getters.isIscLogin || store.getters.isSimulateLogin){//isc
        
         if(to.path=='/isclogin'){ //next()
            next();
          } else { //
              next('/isclogin');
          }
       }else{
        next(`/login?redirect=${to.path}`)
       }
      
    }
  }
})

